import '@servicenow/now-button';
import '@servicenow/now-icon';

import { actionTypes, createCustomElement } from '@servicenow/ui-core';

import { isSlotEmpty } from '@devsnc/sn-controls-common';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import styles from './annotation.scss';
import { t } from 'sn-translate';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_RENDERED } = actionTypes;

const COMPONENT_NAME = 'sn-record-annotation';
const baseClass = COMPONENT_NAME;

const MAX_CONTENT_HEIGHT = 60;
const TRUNCATE_HEIGHT = 32;

function getContentHeight(contentRef) {
	return contentRef ? contentRef.offsetHeight : 0;
}

export function getExpandLabel(expanded) {
	return expanded ? t('Show Less') : t('Show More');
}

function updateContentHeight(contentRef, updateState, setExpanded) {
	const contentHeight = getContentHeight(contentRef);
	const isOverMaxHeight = contentHeight > MAX_CONTENT_HEIGHT;
	const newState = {
		contentHeight,
		isOverMaxHeight
	};
	if (setExpanded) {
		newState.expanded = !isOverMaxHeight;
	}
	updateState(newState);
}

export function view(state, { updateState }) {
	const { important, lineSeparator, sectionSeparator } = state.properties;
	const { componentId } = state;
	const { isOverMaxHeight, expanded, slotEmpty } = state;

	const contentId = `${componentId}-content`;

	const showIcon = !lineSeparator && !sectionSeparator && !slotEmpty;

	const overflowStyle =
		isOverMaxHeight && !expanded
			? { height: `${TRUNCATE_HEIGHT}px`, overflowY: 'hidden' }
			: { height: 'auto' };

	return (
		<div
			ref={element => updateState({ containerRef: element })}
			class={{
				[baseClass]: true,
				[`${baseClass}__error`]: important
			}}
			role="status"
			tabIndex="0"
			aria-label={t('Field annotation')}
			aria-describedby={contentId}
		>
			{lineSeparator && !sectionSeparator ? (
				<hr className="line-separator" />
			) : (
				<Fragment />
			)}
			{sectionSeparator ? <hr className="section-separator" /> : <Fragment />}
			<div
				id={contentId}
				class={{
					[`${baseClass}-content`]: true,
					'has-separator': sectionSeparator || lineSeparator
				}}
			>
				{showIcon ? (
					<div className={`${baseClass}-content-icon`}>
						<now-icon
							icon="circle-info-outline"
							size="md"
							class={{
								'context-form-annotation': true,

								'has-error': important
							}}
						/>
					</div>
				) : (
					<Fragment />
				)}
				<div
					style={overflowStyle}
					class={{
						details: true,
						'is-section': sectionSeparator,
						collapsed: !state.expanded && isOverMaxHeight
					}}
				>
					<div ref={element => updateState({ contentRef: element })}>
						<slot />
					</div>
				</div>
			</div>
			{isOverMaxHeight ? (
				<div
					class={{
						[`${baseClass}-content-toggle`]: true,
						'has-icon': showIcon
					}}
				>
					<now-button-bare
						label={getExpandLabel(expanded)}
						size="sm"
						variant="secondary"
						tooltipContent={t('Toggle Content')}
						on-click={() => {
							updateState({
								expanded: !state.expanded
							});
						}}
					/>
				</div>
			) : (
				<Fragment />
			)}
		</div>
	);
}

/**
 *
 * Simple Annotation component to wrap around a set of Elements
 *
 * ```jsx
 * <div>
 * 	<sn-record-annotation>
 * 		<h5>This is a header</h5>
 * 		<p>
 * 			Body with a <button>button</button> or any arbitrary <b>tag</b>.
 * 		</p>
 * 		<p>This is a footer</p>
 * 	</sn-record-annotation>
 *
 * 	<sn-record-annotation important>
 * 		<h5>This is an annotation with the important attr set</h5>
 * 	</sn-record-annotation>
 * </div>
 * ```
 *
 * @seismicElement sn-record-annotation
 * @summary Annotation Component.
 *
 **/
createCustomElement(COMPONENT_NAME, {
	type: snabbdom,
	view,
	styles,
	initialState: {
		contentHeight: 0,
		isOverMaxHeight: false,
		expanded: true,
		contentRef: null,
		containerRef: null
	},
	actions: {
		COMPONENT_BOOTSTRAPPED: {},
		COMPONENT_RENDERED: {}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: {
			effect({ state, updateState }) {
				const { containerRef, contentRef } = state;
				// Add a listener that updates the state if the slot content changes.
				// NOTE: This doesn't fire unless the actual content changes, so you may
				//       need to change the slotted component's key or tag type.
				const slot = containerRef && containerRef.querySelector('slot');
				slot &&
					slot.addEventListener('slotchange', function() {
						updateContentHeight(contentRef, updateState);
					});

				// On bootstrap, set the content to be not expanded if it is too tall
				updateContentHeight(contentRef, updateState, true);
			}
		},
		[COMPONENT_RENDERED]: {
			effect({ state, updateState }) {
				const { contentRef } = state;
				const slot = contentRef && contentRef.querySelector('slot');
				// Remove tabIndex from container if slot is empty
				if (slot && isSlotEmpty(slot)) {
					state.containerRef.removeAttribute('tabIndex');
				}
				// TODO: Add tabIndex attr if not empty (we would need to remember the old
				// TODO: tabIndex when we removed it).
				updateContentHeight(contentRef, updateState);
			}
		}
	},
	properties: {
		/**
		 * Flag to toggle the info icon color.
		 * If exists, it will be red. The default is blue.
		 *
		 * @type {boolean}
		 */
		important: { default: false, schema: { type: 'boolean' } },

		/**
		 * Flag to add line separator above header.
		 *
		 * @type {boolean}
		 */
		lineSeparator: { default: false, schema: { type: 'boolean' } },

		/**
		 * Flag to add section separator below header.
		 *
		 * @type {boolean}
		 */
		sectionSeparator: { default: false, schema: { type: 'boolean' } }
	}
});

export default COMPONENT_NAME;
